import {mapState,mapActions,mapMutations} from 'vuex'
export default {
  name: "myfoot2",
  data(){
    return {
      select:-1,
      icons:[
        {
          class:'icon-jingxuanshipin',
          name:'精选',
          path:'/',
          flag:true
        },
        {
          class:'icon-shipu',
          name:'食谱',
          path:'/res',
          flag:false
        },
        {
          class:'icon-icon-',
          name:'收藏',
          path:'/fond',
          flag:false
        },
        {
          class:'icon-wode',
          name:'我的',
          path:'/user',
          flag:false
        },
      ],
      chosen:'/',
      but:true
    }
  },
  methods:{
    ...mapMutations(['changeTran','changeHead']),
    selected(n){
      let nn=localStorage.getItem('nth')
      let type=n>nn?'fadeInRight':'fadeInLeft'
      this.changeTran(type)
      localStorage.setItem('nth',n)
      this.changeHead(this.icons[n].name)
      this.select=n
      window.location.hash='#'+this.icons[n].path
    }
  }
}



