import {mapState,mapActions,mapMutations} from 'vuex'
import VueImgInputer from 'vue-img-inputer'
export default {
    name: "user",
    components: {
      VueImgInputer
    },
    data(){
      return{
        bImg:{backgroundImage: "url(" + require("@/assets/3.jpg") + ")"},
        arr:[
          {
            title:'我的订单',
            icon:'icon-yemian',
            back:'rgb(241,158,99)'
          },
          {
            title:'我的课程',
            icon:'icon-fafangzhengshu',
            back:'rgb(243,179,119)'
          },
          {
            title:'签到奖励',
            icon:'icon-qiandao',
            back:'rgb(255,192,99)'
          },
          {
            title:'我的足迹',
            icon:'icon-zuji',
            back:'rgb(197,203,140)'
          },
          {
            title:'地址管理',
            icon:'icon-dizhi',
            back:'rgb(168,2099,167)'
          },
          {
            title:'帮助中心',
            icon:'icon-iconfonticon4',
            back:'rgb(133,218,197)'
          },

        ],
        userName:'点击编辑您的昵称',
        write:false
      }
    },
  // updated(){
  //   console.log(this.collect)
  // },
  created(){
    if(localStorage.getItem('userName')==undefined){
      this.userName='点击修改您的昵称'
    }else{
      this.userName=localStorage.getItem('userName')
    }
  },
  computed:{
    ...mapState(['collect']),
    num(){
      // console.log(this.collect)
      return this.collect.length
    }
  },
  methods:{
    nick(){
      this.write=true
    },
    writeStart(){
      this.userName=''
    },
    writeDone(){
      this.write=false
      if(this.userName==''){
        this.userName='点击修改您的昵称'
      }
      localStorage.setItem('userName',this.userName)
    },
    fileChange(file, name) {
      console.log('File:', file);
      console.log('FileName:', name);
    }
  }
}



