import {mapState,mapActions,mapMutations} from 'vuex'
export default {
  name: "my-fond",
  created(){
      this.fondIn()
  },
  data(){
    return{
      shopList:[]
    }
  },
  computed:{
    ...mapState(['collect']),
    shop(){
     return this.collect.map(item=>{
        return item;
      })
    }

  },
  watch:{
    shop(n){
      this.shopList=n
    }
  },
  methods:{
    ...mapMutations(['changeP','changeName']),
    ...mapActions(['fondIn','fondDel']),
    jump(id,title,x){
      this.changeP(id)
      this.changeName(title)
      window.location.hash='#/pro'
      sessionStorage.setItem(x.id,JSON.stringify(x)) //暂时存放数据
    },
    unfond(id,n){
      this.fondDel(id,3)
      this.shopList.splice(n,1)
    }
  }

}



