
import {mapState,mapActions,mapMutations} from 'vuex'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "head-title",
components:{
ViewBox,XHeader
},
methods:{
...mapMutations(['changeTran']),
back(){
this.changeTran('fadeInLeft')
this.$router.back(-1)
}
},
computed:{
...mapState(['headTitle']),
hTitle(){
if(this.headTitle=='精选'||this.headTitle==undefined){
return 'DayDayCook'
}else{
return this.headTitle
}
}
}
}



