import Vue from 'vue'
import Vuex from 'vuex'
import vueJsonp from 'vue-jsonp'
import $ from 'jquery'
import axios from 'axios'
import qs from 'qs'

Vue.use(vueJsonp)
Vue.use(Vuex)
// Vue.use(qs)
// Vue.use(axios)
// Vue.prototype.$http = axios


export default new Vuex.Store({
  state: {
  	p1:[],
  	food:['hhh'],
  	shop:[],
    pro:'',//单品页的id
    item:[],
    name:'',
    tran:'zoomIn',
    collect:[],//收藏的菜谱
    news:[],//网易新闻
    headTitle:'DayDayCook'
  },
  mutations: {
  	changep1(state,v){
  		state.p1=v
  	},
  	changefood(state,v){
  		state.food=v
  	},
  	changeshop(state,v){
  		state.shop=v
  	},
    changeP(state,v){
      state.pro=v
    },
    changeItem(state,v){
      state.item=v
    },
    changeName(state,v){
      state.name=v
    },
    changeTran(state,v){
      state.tran=v
    },
    changeCollect(state,v){
      state.collect=v
    },
    changeNews(state,v){
  	  state.news=v
    },
    changeHead(state,v){
  	  state.headTitle=v
    }
  },
  actions: {
    taoBao(context,{word,length,fun}) { //传入搜索词与请求数量
      Vue.jsonp('https://s.taobao.com/search?&ajax=true', { q: word }).then(data => {
        let arr = data.mods.itemlist.data.auctions.slice(length[0], length[1])
        console.log(arr)
        fun.map(item=>{
          context.commit(item,arr)
        })
      })
    },
    baidu(context,{word,length,fun}){ //传参同上
    	Vue.jsonp('https://image.baidu.com/search/acjson',
         {
            tn:'resultjson_com',
            ipn:'rj',
            word: word,
            pn: 0,
            rn: length,
        }).then(res=>{

        		let arr = res.data.filter(item=>{
              return item.fromURL
            })

        	// console.log(arr)
        	fun.map(item=>{
        		context.commit(item,arr)
        	})
        })
    },
    fondOf(context,id){//我的收藏
      console.log(id)
      let x=JSON.parse(sessionStorage.getItem(id))
      // console.log(x)
      $.ajax({
        type:'post',
        url:'http://nereus.gotoip2.com/daydaycook/api/myfond.php',
        data:{
          flag:1,
          src:JSON.parse(sessionStorage.getItem(id)).src,
          title:x.title,
          des:x.des,
          view:x.view,
          id:x.id
        }
      }).then(res=>{
        console.log(res)
      })
    },
    fondIn(context){
      // $.ajax({
      //   type:'post',
      //   url:'http://nereus.gotoip2.com/daydaycook/api/myfond.php',
      //   data:{flag:2}
      // }).then(res=>{
      //   console.log('添加完成')
      //   console.log()
      //   // console.log(JSON.parse(res))
      //   context.commit('changeCollect',JSON.parse(res))
      // })
      axios({
        method:'post',
        url:'http://nereus.gotoip2.com/daydaycook/api/myfond.php',
        data:qs.stringify({
          flag:2
        }),
        // headers:{
        //   'Content-Type': 'application/x-www-form-urlencoded',
        // }
      }).then(res=>{
        console.log('-----添加完成')
        console.log(JSON.stringify(res))
        // console.log(JSON.parse(res.data))
        context.commit('changeCollect',res.data)
      }).catch(e => {
        console.log(e);
      })
    },
    fondDel(context,id){
      axios({
        method:'post',
        url:'http://nereus.gotoip2.com/daydaycook/api/myfond.php',
        data:qs.stringify({
          flag:3,
          id:id
        }),
      })
      // $.ajax({
      //   type:'post',
      //   url:'http://nereus.gotoip2.com/daydaycook/api/myfond.php',
      //   data:{
      //     id:id,
      //     flag:3
      //   }
      // }).then(res=>{
      //   // console.log(res)
      // })
    },
    getWang(context){
      console.log(Vue.http)
      Vue.jsonp('http://3g.163.com/touch/jsonp/sy/recommend/0-9.html',{callback:'getNews'}).then(res=>{
        console.log(res.list.slice(0,4))
        let arr=res.list.slice(0,4)
        context.commit('changeNews',arr)
      })
    },
    //购物车
    carin(context,name){
      vueJsonp('https://s.taobao.com/search?&ajax=true',{q:name}).then(res=> {
          let da = res.mods.itemlist.data.auctions[0]
          context.dispatch('putin',{da})
      })
      // $.ajax({
      //   url:'https://s.taobao.com/search?&ajax=true',
      //   type:'get',
      //   dataType:'jsonp',
      //   data:{
      //     q:name
      //   }
      // }).then(res=> {
      //   let da = res.mods.itemlist.data.auctions[0]
      //   context.dispatch('putin',{da})
      // })
    },
    putin(da){
      axios({
        url:'http://nereus.gotoip2.com/daydaycook/api/car.php',
        method:'post',
        data:qs.stringify({
          title:this.name,
          pic:da.pic_url,
          price:da.view_price,
          flag:1
        })
      })
    }
  }
})



