
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default{
name:'shopCar',
components:{
Panel,Group,Radio
},
methods: {
select(n){
let id=this.checkedId.indexOf(n)
if(id>=0){
this.checkedId.splice(id,1)
this.all-=this.shopCar[n].price
}else{
this.checkedId.push(n)
this.all+=this.shopCar[n].price
}
}
},
computed:{

},
data () {
return {
shopCar:[
{
src:require('@/assets/0.jpg'),
tit:'《魔法风云纪》是一款以希腊神话体系为蓝本改编的即时操作战斗的RPG',
price:parseInt(Math.random()*500+100)
},
{
src:require('../assets/1.jpg'),
tit:'《魔法风云纪》是一款以希腊神话体系为蓝本改编的即时操作战斗的RPG',
price:parseInt(Math.random()*500+100)
},
{
src:require('../assets/2.jpg'),
tit:'《魔法风云纪》是一款以希腊神话体系为蓝本改编的即时操作战斗的RPG',
price:parseInt(Math.random()*500+100)
},
],
all:0,
checkedId:[]
}
}
}



