
import {mapState,mapActions,mapMutations} from 'vuex'

export default {
name:'recipes',
data(){
return{
pick:['三明治','虾','蟹','快手菜','养颜食品'],
shopList:[],

}
},



created(){

this.taoBao({word:'零食摄影',length:[0,18],fun:['changeshop']})

},
computed:{
...mapState(['food','shop','shopR','tran']),
Shop(){

return this.shop.map(item=>{
return {



title:item.raw_title,
des:item.title.replace(/<[^<>]+>/g,''),
src:item.pic_url,
id:item.user_id,
view:item.view_sales.match(/\d+/g)[0],
like:false,
}
})

}
},
watch:{
Shop(n){

this.shopList=n

}
},
methods:{
...mapActions(['taoBao','baidu','fondOf','fondDel']),
...mapMutations(['changeshop','changeP','changeName']),
picked(n){

this.taoBao({word:n,length:[0,20],fun:['changeshop']})

},
jump(id,title,x){
this.changeP(id)
this.changeName(title)
window.location.hash='#/pro'
sessionStorage.setItem(x.id,JSON.stringify(x)) 
},
fond(x){
console.log(x.like)
if(x.like===undefined){
console.log('undef')
x.like=true
}else {
x.like=!x.like
}
if(x.like==true){
sessionStorage.setItem(x.id,JSON.stringify(x))
this.fondOf(x.id)
}else{
this.fondDel(x.id)
sessionStorage.removeItem(x.id)
console.log('删除成功')
}
}
}
}



