
import {mapState,mapActions,mapMutations} from 'vuex'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Panel from 'vux/src/components/panel/index.vue'

import $ from 'jquery'
export default {
name: "product",
components:{
ViewBox,Scroller,Swiper,Panel
},
data(){
return{
title:'',
first:'',
itemList:new Array(),
type: '5',
newsList: [],
}
},
created(){
$.ajax({
url:'../assets/ddtt.json',
type:'get',
dataType:'jsonp'
}).then(res=>{
console.log('这是JSON'+res)
})
this.first=JSON.parse(sessionStorage.getItem(this.pro)).src
this.title=this.pro
console.log(this.pro)
$.ajax({
type:'post',
url:'http://nereus.gotoip2.com/daydaycook/api/qq.php',
data:{id:this.pro},
jsonpCallback:'detail_recommend_bought'
}).then(res=>{

console.log(res)
let data = JSON.parse(res.replace(/detail_recommend_bought\(/g, "").replace(/\}\)\;/g, "}"));
console.log(data)
let arr=  data.result.map(item=>{
return{
src:item.pic,
des:item.itemName
}
})
this.changeItem(arr)
})
this.getWang()
console.log(this.item)
},
computed:{
...mapState(['pro','item','name','news']),
itemPage(){
return this.item
},
new(){
console.log(this.news)
return this.news.map(item=>{
if(item.picInfo[0]){
return {
src: item.picInfo[0].url,

title: item.title,
desc: item.digest,

}
}
})
}

},
watch:{
itemPage(n){



console.log(this.item)
this.itemList=n
this.changeItem(n)
},
new(n){
this.newsList=n
}
},
methods:{
...mapMutations(['changeItem']),
...mapActions(['baidu','fondOf','getWang']),
collect(){
this.fondOf(this.pro)
}
}
}



