import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import mainPage from '@/components/mainPage'
import banner from '@/base/banner'
import recipes from '@/components/recipes'
import shopCar from "@/components/shopCar"
import pro from '@/components/product'
import fond from '@/components/myFond'
import user from '@/components/user'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name:'main',
      component:mainPage
    },
    {
      path:'/ban',
      name:'banner',
      component:banner
    },
    {
      path:'/res',
      name:'recipes',
      component:resolve=>require(['@/components/recipes.vue'],resolve)
    },
    {
      path:'/car',
      name:'shopcar',
      component:shopCar
    },
    {
      path:'/pro',
      name:'product',
      component:pro
    },
    {
      path:'/fond',
      name:'fond',
      component:fond
    },
    {
      path:'/user',
      name:'user',
      component:user
    },
    // {
    //   path:'/*',
    //   redirect:{
    //     path:'/ban'
    //   }
    // }
  ]
})



