
import {mapState,mapActions,mapMutations} from 'vuex'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
name: "my-foot",
components:{
ViewBox,Tabbar, TabbarItem
},
props:['pa'],
data(){
return {
icons2:[
{
class:'icon-jingxuanshipin',
name:'精选',
path:'/',
flag:true
},
{
class:'icon-shipu',
name:'食谱',
path:'/res',
flag:false
},
{
class:'icon-icon-',
name:'收藏',
path:'/fond',
flag:false
},
{
class:'icon-wode',
name:'我的',
path:'/user',
flag:false
},
],
chosen:'/',
but:true
}
},
created(){
console.log(this.bottom)
},
computed:{
s(){
return this.$route.path
},
},
watch:{
s(n) { //手动实现路由刷新
console.log(n)
this.icons2.map(item => {
if (item['path'] == n) {
item.flag = true
} else {
item.flag = false
}
})
this.but=n=='/pro'?false:true
},
},

methods:{
...mapMutations(['changeTran','changeHead']),
selected(n){
let nn=localStorage.getItem('nth')
let type=n>nn?'fadeInRight':'fadeInLeft'
this.changeTran(type)
localStorage.setItem('nth',n)
this.changeHead(this.icons2[n].name)
this.chosen=n

}
}

}



